<template>
  <div class="edit-form mt-5">
    <h1 class="d-flex justify-content-center">{{ cliente.nombre }}</h1>

    <div>
      <div class="mt-5">
        <h5>A pagar:</h5>
      </div>
    </div>

    <div>
      <div>
        <h3>${{ cliente.saldo }}</h3>
      </div>
    </div>

    <div>
      <button @click="push(clienteId)" class="btn btn-success my-4 w-50">
        Realizar pago
      </button>
    </div>
  </div>
</template>

<script>
import { getCliente } from "@/firebase";
import { useRoute } from "vue-router";
import { computed, onMounted, reactive } from "vue";

export default {
  setup() {
    const route = useRoute();
    const clienteId = computed(() => route.params.id);
    const cliente = reactive({ nombre: "", saldo: null });

    onMounted(async () => {
      const clientefb = await getCliente(clienteId.value);
      cliente.nombre = clientefb.nombre;
      cliente.saldo = clientefb.saldo;
    });
    return { clienteId, cliente };
  },
  methods: {
    push(id) {
      this.$router.push("/clientes/" + id + "/pago/");
    },
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>